import * as React from "react";
import { useSelector } from "react-redux";
import TableCommon from "../../common/table";
import "./styles.css";
import { Image, Popover, Space } from "antd";

export default function UsersTable() {
  const brandsGetState = useSelector((state) => state.users);
  const rows = brandsGetState.UserGet?.data;

  const data = [];
  rows?.map((elem, index) => {
    data.push({
      data: index + 1,
      key: elem.id,
      Фото: (
        <Image
          width={60}
          height="100%"
          style={{
            aspectRatio: "1 / 1",
            borderRadius: "20px",
            objectFit: "cover",
          }}
          src={elem.image1}
        />
      ),
      Ии: elem.surname + " " + elem.name,
      Электроннаяпочта: elem.email,
      Телефон: elem.phone,
    });
  });

  const columns = [
    {
      title: "№",
      dataIndex: "data",
      key: "data",
      width: 50,
      align: "center",
    },
    {
      title: "Фото",
      dataIndex: "Фото",
      key: "Фото",
      align: "center",
      width: "120px",
    },
    {
      title: "Имя и фамилия",
      dataIndex: "Ии",
      key: "Ии",
    },
    {
      title: "Электронная почта",
      dataIndex: "Электроннаяпочта",
      key: "Электроннаяпочта",
    },
    {
      title: "Телефон",
      dataIndex: "Телефон",
      key: "Телефон",
    },
  ];
  return (
    <TableCommon
      bordered
      columns={columns}
      data={data}
      pagination={false}
      scroll={{
        y: 330,
      }}
    />
  );
}
